import './App.css';
import React, {useState} from "react";
import {
  Link,
  Outlet
} from "react-router-dom";
import logo from "./logo.png";

export const ENDPOINT = "https://theboysbowling.herokuapp.com";

function App() {
  return (
      <div className="App">
        <div className='logo'>
          <img src={logo} />
        </div>
        <Outlet />
      </div>
  );
}

export default App;
